import React, { useState } from 'react';
export const AddTodo = ({addTodo}) => {
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");
    const submit = (e)=>{
        e.preventDefault();
        if(!title || !description){
            alert("Title and Description can not be blank!");
        }
        else{
            addTodo(title,description);
            setTitle("");
            setDescription("");
        }
        
    } 
    return (
        <div className = "container my-3" >
            <h3>Add a Todo</h3>
        <form onSubmit={submit}>
            <div className="form-group mb-3">
                <label htmlFor="title">Todo Title</label>
                <input type="text" value={title} onChange={(e)=>{setTitle(e.target.value)}} className="form-control" id="title" aria-describedby="emailHelp" placeholder="Enter tile of the todo"/>
            </div>
            <div className="form-group mb-3">
                <label htmlFor="description">Todo Description</label>
                <input type="text" value={description} onChange={(e)=>{setDescription(e.target.value)}} className="form-control" id="description" aria-describedby="emailHelp" placeholder="Enter description of the todo"/>
            </div>
            <button type="submit" className="btn btn-sm btn-success">Add Todo</button>
        </form>
        </div>
    )
}
