import React from 'react'

export const About = () => {
    let imgStyle = {
        display: "block",
        margin: "auto",
        width: "50%",
    }
    let divStyle = {
        minHeight: "70vh"
    }
    return (
        <div style={divStyle}> 
            <img style={imgStyle}src="https://www.deweyjames.com/wp-content/uploads/2018/04/under-construction-1000x500.jpg" alt="" />
        </div>
    )
}
